#card-back {
    width: 80%;
    height: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
}

#card-back h3 {
    width: 100%;
    height: 100%;
    font-family: 'IM Fell English', cursive;
    font-size: 1.1rem;
    line-height: 1.3em;
    text-align: center;
    resize: none;
}

#card-back button {
    position: absolute;
    bottom: 0;
    right: 0;
}


