#card-body-container {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden;
}

#card-body {
    width: 45%;
    max-width: 400px;
    height: 95%;
    /* aspect-ratio: 2/3; */
    position: relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
            box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

#card-body-container h4 {
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

#card-frw-arrow,
#card-back-arrow {
    width: 27.5%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

#card-frw-arrow h4,
#card-back-arrow h4 {
    font-family: 'Nunito', sans-serif;
    font-size: 1rem;
    color: grey;
    margin-bottom: 10%;
}

#card-frw-arrow h4:hover,
#card-back-arrow h4:hover {
    color: #dc004e;
}



