#media-catalog {
  width: 100%;
  /* height: 90vh; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
  border-radius: 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
          box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.media-catalog-subcontainer {
  width: 90%;
  margin: auto;
}

.media-catalog-subcontainer h1 {
  font-family: 'Norican', cursive;
}

.media-catalog-subcontainer p {
  font-family: 'Nunito';
}

.catalog-tab {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.nav-tabs {
  border-bottom: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.nav-tabs .nav-link {
  color: #000;
  text-decoration: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  border-left: 1px solid #E1C16E;
  border-top: 1px solid #E1C16E;
  border-right: 1px solid #E1C16E;
  border-bottom: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #000;
}

.nav-tabs .nav-link:hover {
  border-top: 1px solid #E1C16E;
  border-left: 1px solid #E1C16E;
  border-right: 1px solid #E1C16E;
  border-bottom: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #dc004e;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
  border-left: 1px solid #E1C16E;
  border-top: 1px solid #E1C16E;
  border-right: 1px solid #E1C16E;
  border-bottom: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #000;
}

.nav .show .nav-link,
.nav .nav-link:hover:not(.disabled),
.nav .nav-link.active {
  border-left: 1px solid #E1C16E;
  border-top: 1px solid #E1C16E;
  border-right: 1px solid #E1C16E;
  border-bottom: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #000;
}


