.home-slider {
    width: 100%;
    height: 90vh;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border-radius: 25px;
    overflow: hidden;
    -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
            box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.slide {
    position: relative;
    opacity: 0;
    -webkit-transition-duration: 1s ease;
         -o-transition-duration: 1s ease;
            transition-duration: 1s ease;
}

.slide.active {
    position: relative;
    width: 100%;
    height: 90vh;
    opacity: 1;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
}

.right-arrow,
.left-arrow {
    width: 5.5%;
    height: 5.5%;
    position: absolute;
    top: 50%;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background: -webkit-gradient(linear, left top, left bottom, from(#a95), to(#fea));
    background-size: 200% auto;
    border: none;
    border-radius: 5px;
    -webkit-box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 1),
    0px 1px 3px rgba(0, 0, 0, 0.3);
            box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 1),
    0px 1px 3px rgba(0, 0, 0, 0.3);
    outline: none;
    border: 1px solid #ba6;
    opacity: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-animation: show-up 1s 9s forwards;
            animation: show-up 1s 9s forwards;
}

.left-arrow svg,
.right-arrow svg {
    color: #e9e9e9;
}

.right-arrow svg:hover,
.left-arrow svg:hover {
    color: #c9c9c9;
}

.right-arrow {
    right: 10px;
}

.left-arrow {
    left: 10px;
}

.container-dots {
    position: absolute;
    bottom: 15px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    display: -ms-flexbox;
    display: flex;
    opacity: 0;
    z-index: 10;
    -webkit-animation: show-up 1s 9s forwards;
            animation: show-up 1s 9s forwards;
}

.dot {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 1px solid #f1f1f1;
    margin: 0 5px;
    background: rgba(255, 255, 255, .9);
}

.dot.active {
    background: #dc004e;
}


