.test-test {
    /* transform:rotate(46deg) */
    -webkit-animation: test-test 1s ease infinite;
            animation: test-test 1s ease infinite;
    /* color: red !important; */
}

@-webkit-keyframes test-test {
    0% {-webkit-transform:rotate(-46deg);transform:rotate(-46deg)}
    50% {-webkit-transform:rotate(0deg);transform:rotate(0deg)}
    100% {-webkit-transform:rotate(46deg);transform:rotate(46deg)}
}

@keyframes test-test {
    0% {-webkit-transform:rotate(-46deg);transform:rotate(-46deg)}
    50% {-webkit-transform:rotate(0deg);transform:rotate(0deg)}
    100% {-webkit-transform:rotate(46deg);transform:rotate(46deg)}
}



