#contact-list {
  width: 70vw;
  min-height: 90vh;
  /* height: 90vh; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  /* text-align: center; */
  position: absolute;
  margin: auto;
  border: 1px solid #E1C16E;
  border-radius: 25px;
  -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
  rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
          box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
  rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  font-family: 'Nunito',
  sans-serif;
  font-size: .9rem;
  /* overflow: auto; */

}
.white-space {
    width: 100%;
    min-height: 130vh;
    /* height: 90vh; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;

   /* overflow: auto; */
}

.contacts-header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center; 
  -ms-flex-pack: justify; 
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: 5%;
  padding: 0 5%;
}

.contacts-header h2 {
  font-family: 'Norican', cursive;
  font-size: 2rem;
  margin: 0;
  display: inline-block;
  width: 60%;
}



.contacts-custom-btn {
  background: -webkit-gradient(linear, left top, left bottom, from(#a95), to(#fea));
  border: 1px solid #ba6;
  background-size: 200% auto;
  color: #ffffff;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  font-family: 'Nunito',
    sans-serif;
  font-size: .8rem;
  font-weight: 700;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.4);
  text-decoration: none;
  cursor: pointer;
  position: inline-block;
  display: inline-block;
  -webkit-box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 1),
    0px 1px 3px rgba(0, 0, 0, 0.3);
          box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 1),
    0px 1px 3px rgba(0, 0, 0, 0.3);
  outline: none;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
  
}

.contacts-btn {
  padding: 10px 20px;

}

.contacts-custom-btn:hover {
  text-decoration: none;
  color: #ffffff;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
  background: -webkit-gradient(linear, left top, left bottom, from(#fea), to(#a95));
}

.card-body {
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  min-height: 1px;
  padding: 2.25rem;
}

.contacts-frame {
  height: 100%;
  width: 100%;
  border-radius: 25px;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

