.component-cards-list {
    width: 100%;
    /* height: 60vh; */
    padding: 2%;
    text-align: center;

    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    border: 1px solid #E1C16E;
    border-top: hidden;
    margin-bottom: 3rem;
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    /* flex-wrap: wrap;
    overflow-y: scroll; */
}

.cards-list-wrapper {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.cards-list-wrapper2 {
    width: 33.33%;
    height: 40%;
    margin-bottom: 2%;
}

.cards-list-wrapper2 img {
    width: 40%;
    border: 1px solid lightgray;
}

.cards-list-wrapper2 p {
    display: inline-block;
    color: #000;
    margin: 0;
}

.cards-list-wrapper2 p:hover {
    color: #dc004e;
}

.cards-list-btn-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}


