.handwriting {
    width: 100%;
    top: 0;
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
    -webkit-transition-delay: 1s;
         -o-transition-delay: 1s;
            transition-delay: 1s;
    position: absolute;
    
}

.handwriting svg {
    width: 100%;
}

.text {
    stroke-dasharray: 1200;
    stroke-dashoffset: 1200;
    -webkit-animation: animate 10s 4s linear 1 forwards;
            animation: animate 10s 4s linear 1 forwards;
    stroke: #000000;
}

@-webkit-keyframes animate {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes animate {
    to {
        stroke-dashoffset: 0;
    }
}


