#catalog-home {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 25px;
    position: relative;
    overflow: hidden;
    background-color: #ffffff;
}

.catalog-home-subcontainer {
    width: 90%;
}

.catalog-home-subcontainer h1 {
    font-family: 'Norican', cursive;
    margin-top: 5vmin;
}

.catalog-home-subcontainer p {
    font-family: 'Nunito', sans-serif;
}

/* Scroll Icon */

.img-catalog-scroll-up {
    margin: 0;
    padding: 0;
    position: absolute;
    z-index: 6;
    top: 0;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    color: #000000;
    font-family: 'Nunito', sans-serif;
}

.img-catalog-scroll-up:hover,
.img-catalog-scroll-down:hover {
    text-decoration: none;
    color: #dc004e;
}

.img-catalog-scroll-down {
    margin: 0;
    padding: 0;
    position: absolute;
    z-index: 1;
    top: 95%;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    color: #000000;
    font-family: 'Nunito', sans-serif;
}



