/* Flip Card */

.editor-flip-card {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: perspective(1000px);
            transform: perspective(1000px);
    -webkit-box-shadow: 10px 20 px 40 px rgba (0, 0, 0, 0, 0.25);
            box-shadow: 10px 20 px 40 px rgba (0, 0, 0, 0, 0.25);
    position: absolute;
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    z-index: 5;
}

/* Flip Card - Front */

.editor-flip-card .editor-imgBox {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    background: #ffffff;
    -webkit-transition: 2s;
    -o-transition: 2s;
    transition: 2s;
    -webkit-box-shadow: 10px 20 px 40 px rgba (0, 0, 0, 0, 0.25);
            box-shadow: 10px 20 px 40 px rgba (0, 0, 0, 0, 0.25);
}

.rotate {
    -webkit-transform: rotateY(-130deg);
            transform: rotateY(-130deg);
}

.rotate-close {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
}

.editor-flip-card .editor-imgBox img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -o-object-fit: cover;
       object-fit: cover;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-box-shadow: 10px 20 px 40 px rgba (0, 0, 0, 0, 0.25);
            box-shadow: 10px 20 px 40 px rgba (0, 0, 0, 0, 0.25);
}

.editor-flip-card .editor-imgBox img:nth-child(2) {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    -webkit-box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
            box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.cover-text-editor {
    position: absolute;
    width: 100%;
    height: 12%;
    top: 80%;
    left: 0;
    z-index: 1;
    -webkit-transform-origin: left;
        -ms-transform-origin: left;
            transform-origin: left;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 2s;
    -o-transition: 2s;
    transition: 2s;
    background-color: #ffffff;

}

.cover-text-editor h4 {
    font-family: 'Sacramento';
    font-size: 1.3rem;
    margin-top: 1.5%;
    
}

/* Flip Card - Text */

.editor-flip-card .editor-flip-card-text {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    background: #ffffff;
}

.editor-flip-card .details .content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.editor-flip-card .editor-flip-card-text h2 {
    font-family: 'Norican', cursive;
    font-size: 1.6rem;
    color: #dc004e;
    line-height: 1em;
    margin: 10% 0;
}

.editor-flip-card .editor-flip-card-text h3 {
    font-family: 'IM Fell English', cursive;
    font-size: 1.2rem;
    line-height: 1.3em;
    margin-top: 12%;
}

.editor-flip-card .editor-flip-card-text h4 {
    font-family: 'Norican', cursive;
    font-size: 1.5rem;
    line-height: 1em;
    margin-top: 20%;
}

.editor-flip-card .details .content {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    margin-top: 10px;
}



