#portfolio-filter {
    width: 90%;
}

.component {
    width: 100%;
    height: 100%;
    padding: 2%;
    text-align: center;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    border: 1px solid #E1C16E;
    border-top: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 5rem;
}

.picture {
    width: 30%;
    height: auto;
    /* box-sizing: border-box; */
    margin: 10px;
}

.picture:hover {
    border: 2px solid #dc004e;
    opacity: 0.8;
}

.link-text-catalog {
    font-family: 'Nunito', sans-serif;
    color: #000;
}

.link-text-catalog:hover {
    text-decoration: none;
    color: #dc004e;
}



